import Vue from 'vue';
import Vuex from 'vuex';
import clone from '@/models/clone';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    recordList: [] as RecordItem[],
    tagList: [] as Tag[],
    initialLabel: [
      {type: '-', tag: '', icon: 'dining'},
      {type: '-', tag: '', icon: 'vegetables'},
      {type: '-', tag: '', icon: 'daily'},
      {type: '-', tag: '', icon: 'traffic'},
      {type: '-', tag: '', icon: 'snacks'},
      {type: '-', tag: '', icon: 'sports'},
      {type: '-', tag: '', icon: 'recreation'},
      {type: '-', tag: '', icon: 'phone'},
      {type: '-', tag: '', icon: 'clothing'},
      {type: '-', tag: '', icon: 'face'},
      {type: '-', tag: '', icon: 'house'},
      {type: '-', tag: '', icon: 'payment'},
      {type: '-', tag: '', icon: 'travel'},
      {type: '-', tag: '', icon: 'medical'},
      {type: '-', tag: '', icon: 'car'},
      {type: '-', tag: '', icon: 'study'},
      {type: '-', tag: '', icon: 'fast-mail'},
      {type: '+', tag: '', icon: 'salary'},
      {type: '+', tag: '', icon: 'concurrent-post'},
      {type: '+', tag: '', icon: 'red-packet'},
    ]
  },
  mutations: {
    saveRecord(state) {
      window.localStorage.setItem('recordList', JSON.stringify(state.recordList));
    },
    fetchRecords(state) {
      state.recordList = JSON.parse(window.localStorage.getItem('recordList') || '[]') as RecordItem[];
    },
    createRecord(state, record: RecordItem) {
      const record2 = clone(record);
      record2.createAt = new Date().toISOString();
      state.recordList.push(record2);
      this.commit('saveRecord');
    },

    saveTags(state) {
      window.localStorage.setItem('tagList', JSON.stringify(state.tagList));
    },
    fetchTags(state) {
      state.tagList = JSON.parse(window.localStorage.getItem('tagList') || '[]');
      if (!state.tagList || state.tagList.length === 0) {
        for(let i=0;i<=state.initialLabel.length;i++){
          this.commit('createTag',state.initialLabel[i])
        }
      }
    },
    removeTag(state, tagName) {
      for (let i = 0; i <= state.tagList.length; i++) {
        if (state.tagList[i].tag === tagName) {
          state.tagList.splice(i, 1);
          break;
        }
      }
      this.commit('saveTags');
      window.alert('');
    },
    createTag(state, payload) {
      const tagName = payload.tag;
      console.log(tagName);
      const createType = payload.type;
      console.log(createType);
      const icon = payload.icon;
      const tags = state.tagList.map(item => item.tag);
      console.log(tags);
      if (tags.indexOf(tagName) >= 0) {
        window.alert('');
        return 'duplicated';
      }
      state.tagList.push({type: createType, tag: tagName, icon});
      this.commit('saveTags');
      return 'success';

    }
  },
  actions: {},
  modules: {}
});
